@use 'base/mixins' as m;
@use 'base/variables' as v;


.bg{
        width: 100%;
        height: 100vh;
        background-image: url(../img/background.png);
        background-position: center bottom;
        background-size: cover;
        padding: 0 7%;
        color: v.$blanco;

        @include m.tablet{
            background-position: center;
        }
    }
