html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

.bg {
  width: 100%;
  height: 100vh;
  background-image: url(../img/background.png);
  background-position: center bottom;
  background-size: cover;
  padding: 0 7%;
  color: #FFFFFF;
}
@media (min-width: 768px) {
  .bg {
    background-position: center;
  }
}

.nav {
  width: 100%;
  padding: 2rem 0;
  display: flex;
  align-items: top;
  background-color: #0F002B;
  justify-content: space-around;
  border-bottom-left-radius: 1rem;
  border-bottom-right-radius: 1rem;
}
@media (min-width: 768px) {
  .nav {
    align-items: center;
    background-color: unset;
    justify-content: space-between;
  }
}
.nav__logo {
  width: 5rem;
  cursor: pointer;
}
.nav__enlaces {
  display: flex;
  gap: 2rem;
  flex-direction: column;
}
@media (min-width: 768px) {
  .nav__enlaces {
    flex-direction: row;
  }
}
.nav__btn {
  color: #FFFFFF;
  text-decoration: none;
}
.nav__btn:hover {
  text-decoration: underline;
}
.nav__lenguages {
  visibility: hidden;
}
@media (min-width: 768px) {
  .nav__lenguages {
    border-radius: 2rem;
    border: 1px solid #FFFFFF;
    color: white;
    text-decoration: none;
    visibility: visible;
    padding: 1rem 3rem;
  }
}

.contenido {
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .contenido {
    margin-top: 15rem;
  }
}
.contenido__heading {
  font-size: 9rem;
  margin-bottom: 1.5rem;
  -webkit-text-stroke: 2px #0F002B;
  color: white;
}
.contenido__modificado {
  color: #ff960b;
}
.contenido__texto {
  line-height: 2.2rem;
  font-size: 1.4rem;
}
.contenido__btn {
  margin-top: 3rem;
  background-color: #ff960b;
  color: #FFFFFF;
  text-decoration: none;
  padding: 1.5rem 3rem;
  border-radius: 3rem;
  transition: 0.3s;
  text-align: center;
  display: block;
}
@media (min-width: 768px) {
  .contenido__btn {
    display: inline-block;
  }
}
.contenido__btn:hover {
  background-color: #e18000;
}

.coins {
  position: absolute;
  right: 10%;
  bottom: 10rem;
  display: flex;
  align-items: center;
}
@media (min-width: 768px) {
  .coins {
    bottom: 5rem;
  }
}
.coins__sec {
  display: flex;
  align-items: center;
  font-size: 1.4rem;
  margin: 0 1.5rem;
  color: #ff960b;
  border: 1px solid #FFFFFF;
  padding: 1.5rem;
  border-radius: 1rem;
}
@media (min-width: 768px) {
  .coins__sec {
    padding: 2rem 3rem;
  }
}
.coins__images {
  width: 4rem;
  margin: right 1rem;
}
.coins__price {
  margin: 0;
  color: #FFFFFF;
  margin-bottom: 0.5rem;
}
.coins__name {
  margin: 0;
}